body{
  margin-top: 0;
  background-size:cover;
  background-attachment: fixed;
  background-image: url(https://i.pinimg.com/originals/3e/81/94/3e819415c58c3954b2ee79c2f17454c6.jpg) ;
}
.logo{
  width:80px;
}
.marginFilm{
  margin-top: 60px;
}
.borderOfTitle{
  width: 280px;
  height: 140px;
  border: 4px inset #c10000;
  margin: 25px 30px;
  text-align: center ;
  font-weight: bold;
  background-color: rgba(255,255,255,0.7);
  padding-top: 50px;
}
.borderOfTitle:hover{
  border: 4px inset #ffab00;
}
.displayDate{
  background-color: rgba(255,255,255,0.7);
  margin: 50px 0;
}
.colorOfTitle{
  color: yellow;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  text-shadow: red 0 0 2px;
}
.coverForDate{
  background-color: rgba(255,255,255,0.7);
  text-align: center;
  border: 3px inset #c10000;
}
.openingCrawl{
  background-color: #ffffff;
  padding: 15px 45px;
}

@media only screen and (max-width: 702px) {
  .marginFilm {
    margin-top: 20px;
  }
  .borderOfTitle {
    margin: 20px 30px;
  }
}